import React from 'react';
import styled from 'styled-components';
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip } from 'recharts';

const ChartContainer = styled.div`
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ChartTitle = styled.h3`
  font-size: 18px;
  font-weight: 600;
  color: var(--color-text-primary);
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  align-self: flex-start;
  width: 100%;
`;

const LegendContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 16px;
  justify-content: center;
`;

const LegendItem = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: var(--color-text-primary);
`;

const LegendColor = styled.div`
  width: 16px;
  height: 16px;
  border-radius: 4px;
  background-color: ${props => props.color};
`;

const CustomTooltip = styled.div`
  background: rgba(0, 0, 0, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 12px;
  color: white;
  font-size: 14px;
  
  .label {
    font-weight: 600;
    margin-bottom: 4px;
  }
  
  .value {
    color: var(--color-primary);
  }
`;

const DonutChart = ({ data, title, icon, colors = ['#6366f1', '#8b5cf6', '#ec4899', '#f59e0b', '#10b981'] }) => {
  const CustomTooltipComponent = ({ active, payload }) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      return (
        <CustomTooltip>
          <div className="label">{data.name}</div>
          <div className="value">
            {data.value} ({data.percentage}%)
          </div>
        </CustomTooltip>
      );
    }
    return null;
  };

  const processedData = data.map((item, index) => ({
    ...item,
    percentage: ((item.value / data.reduce((sum, d) => sum + d.value, 0)) * 100).toFixed(1)
  }));

  return (
    <ChartContainer>
      <ChartTitle>
        {icon && <span>{icon}</span>}
        {title}
      </ChartTitle>
      <ResponsiveContainer width="100%" height={300}>
        <PieChart>
          <Pie
            data={processedData}
            cx="50%"
            cy="50%"
            innerRadius={60}
            outerRadius={120}
            paddingAngle={2}
            dataKey="value"
          >
            {processedData.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
            ))}
          </Pie>
          <Tooltip content={<CustomTooltipComponent />} />
        </PieChart>
      </ResponsiveContainer>
      <LegendContainer>
        {processedData.map((entry, index) => (
          <LegendItem key={entry.name}>
            <LegendColor color={colors[index % colors.length]} />
            <span>{entry.name}</span>
          </LegendItem>
        ))}
      </LegendContainer>
    </ChartContainer>
  );
};

export default DonutChart;
